import * as types from '../action/types'

export const initialState = {
  data: null,
  type:null,
  err: null
}

export default function englishReducer(state = initialState, action) {
  switch (action.type) {
    case types.GET_HOME_SUCCESS:
      return {
        ...state,
        type:null,
        data: action.data
      }
    case types.GET_HOME_ERR:
      return {
        ...state,
        type:null,
        err: action.err
      }
    default:
      return state
  }
}